import * as React from "react";
import { Title } from "./title/title";
import { Wave } from "./wave/wave";
import "./tide.scss";
export class Tide extends React.Component {
    constructor(props) {
        super(props);
    }
    // clickShare = () => {
    // }
    // clickRefresh = () => {
    // }
    render() {
        const { noaa } = this.props;
        return (React.createElement("div", { className: "tide" },
            React.createElement(Title, { noaa: noaa }),
            React.createElement(Wave, { noaa: noaa })));
    }
}



